@charset "UTF-8";
* {
  padding: 0;
  margin: 0; }

body {
  width: 100%;
  box-sizing: border-box; }

/* ESTILOS HEADER */
.header {
  width: 100%;
  height: 105px;
  box-sizing: border-box;
  position: fixed;
  align-content: center;
  text-transform: uppercase;
  background: rgba(9, 157, 141, 0.95);
  opacity: 95%;
  z-index: 2; }

/* NOMBRE EQUIPO */
.headername {
  width: 100%;
  height: 60px;
  text-align: center;
  opacity: 95%; }

/* MENÚ */
.teamname {
  line-height: 30px;
  font-family: "rubik", sans-serif;
  font-size: 16px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  color: white;
  padding-top: 15px; }

.menu {
  display: block;
  width: 100vw;
  height: 45px; }

.headermenu {
  display: flex;
  flex-direction: row;
  justify-items: center;
  justify-content: space-evenly;
  text-align: center;
  padding-left: 16px;
  padding-right: 17px;
  padding-top: 8px;
  padding-bottom: 7px;
  list-style: none;
  background-color: #14d9c4; }

/* LINKS MENÚ */
.team,
.aboutus,
.contact {
  line-height: 30px;
  font-family: "rubik", sans-serif;
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  text-decoration: none;
  color: white; }

.team {
  width: 56px;
  height: 30px; }

.aboutus {
  width: 121px;
  height: 30px; }

.contact {
  width: 80px;
  height: 30px; }

/* ESTILOS HERO */
.hero-image {
  width: 100vw;
  margin-top: 60px; }

/* LEMA HERO */
.motto {
  position: relative;
  margin-left: 15px;
  margin-right: 15px;
  top: -30px;
  text-justify: center;
  background-color: white; }

.mottotext {
  padding: 15px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal; }

/* ABOUT US */
.story,
.strengths,
.weaknesses,
.adalabers-container {
  margin-left: 15px;
  margin-right: 15px; }

.story {
  margin-top: -66px; }

.triangle-down {
  width: 0;
  height: 0;
  border-left: 31px solid transparent;
  border-right: 31px solid transparent;
  border-top: 31px solid white;
  margin: -1px auto; }

.about-us-container {
  background-color: #b8b8b9; }

.title {
  font-family: "Rubik";
  letter-spacing: normal;
  font-stretch: normal;
  font-style: normal;
  text-align: center; }

.text1,
.text2,
.text3 {
  font-family: "Open Sans";
  text-align: center; }

.text1,
.text2 {
  margin-bottom: 30px; }

.text3 {
  padding-bottom: 45px; }

/* corregido el centro de los títulos */
.title1 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 15px;
  font-weight: 500;
  line-height: 1.88;
  color: #14d9c4;
  padding-top: 30px; }

.title2,
.title3 {
  color: #099d8d;
  height: 45px;
  font-size: 22px;
  font-weight: 500;
  line-height: 2.05; }

/* ADALABERS */
.adalabers-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center; }

.who-we-are-h1 {
  color: #099d8d;
  font-size: 24px;
  font-family: Rubik;
  margin-top: 30px;
  line-height: 45px; }

.girl-container {
  display: flex;
  flex-direction: column;
  align-items: center; }

.girl-image {
  margin-top: 30px;
  margin-bottom: 15px;
  width: 180px;
  border: solid 5px #b8b8b9;
  border-radius: 2%; }

.girl-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px; }

.girl-name {
  color: #000000;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  font-family: Open Sans; }

.girl-description {
  color: #54585a;
  font-size: 16px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  line-height: 24px; }

.icons {
  display: flex;
  width: 25%;
  margin-top: 15px;
  margin-bottom: 15px;
  font-size: 16px;
  justify-content: space-around; }

.icon {
  color: #099d8d; }

.dot {
  color: #54585a; }

/* FOOTER */
footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid #b8b8b9;
  padding-top: 30px;
  padding-bottom: 45px; }

.copy2020 {
  height: 20px;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: #b8b8b9; }

.menu-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  list-style-type: none;
  text-transform: uppercase;
  width: 114px;
  height: 60px;
  opacity: 0.75;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  text-align: center;
  color: #54585a;
  padding-top: 20px;
  padding-bottom: 30px; }

.team-1,
.team-1:visited,
.aboutus-1,
.aboutus-1:visited,
.contact-1,
.contact-1:visited {
  color: inherit;
  text-decoration: none; }

.logo {
  width: 115.9px;
  height: 45px; }

