  .girl-description-1,
  .girl-description-3,
  .girl-description-5 {
    text-align: left; }
  .girl-description-2,
  .girl-description-4 {
    text-align: right; }
  /*---------------ADALABERS SECTION - END---------------*/
  /*---------------FOOTER SECTION - START----------------*/
  footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    border-top: 1px solid #b8b8b9;
    padding-top: 30px;
    padding-bottom: 45px; }
  .copy2020 {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 20px;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.43;
    letter-spacing: normal;
    text-align: center;
    color: #b8b8b9; }
  .menu-footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 114px;
    height: 60px;
    opacity: 0.75;
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.43;
    letter-spacing: normal;
    text-align: center;
    color: #54585a;
    padding-top: 20px;
    padding-bottom: 30px; }
  .logo {
    width: 115.9px;
    height: 45px; } }

